import React from "react";
import styled from "styled-components";
import logo from "../assets/ic_launcher.png";

function Loading() {
  return (
    <Wrapper>
      <div className="containerDiv">
        <img src={logo} alt="" />  
        <span>loading...</span>
      </div>
    </Wrapper>
  );
}

const Wrapper = styled.div`
  margin:auto;
  height:100vh;
  background-color:lightgrey;
  text-align:center;
  align-items:center;

  .containerDiv {
    padding-top:90px;
    display: grid;
    grid-auto-flow:row;
    grid-gap: 5px;
    place-items:center;
  }

  span {
    color:black;
    font-size: 25px;
  }

`;

export default Loading;
