export class UserModel {
    
    state = {};

    constructor() {
        this.state = {
            batteryLevel: 0,
            batteryState: null,
            currBatteryTime: Date.now(),
            notifyBatteryLevel: 100,
            notifyBatteryState: true,
            notificationSent: false,
        };
    }
    
    setUserState(objState) {
        this.state = {...objState}
    }

    static getBatteryStateText(batState) {
        if(batState != null) {
            if (batState === true) {
            return 'Charging';
            } else if (batState === false) {
            return 'Discharging';
            } else {
            return 'Full';
            }
        }
        else {
            return '';
        }  
    }

    static getBatteryLevelText(batLvl) { 
        return (batLvl * 100) + '%';
    }

    static getBatteryTimeText(timeTxt) {
        const dateVal = new Date(timeTxt)
        return dateVal.toLocaleDateString() + ' ' + dateVal.toLocaleTimeString();
    }
    
}